<template lang="jade">
  .page
    top-nav-bar
    main.content
        transition( name="fade", mode="out-in")
          router-view
    footer-bar
</template>

<script>
  import TopNavBar from './TopNavBar.vue'
  import FooterBar from './FooterBar.vue'

  export default {
    components: {
      [TopNavBar.name]: TopNavBar,
      [FooterBar.name]: FooterBar
    },
    data () {
      return {}
    }
  }
</script>

<style>
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .4s
  }

  .fade-enter,
  .fade-leave-to
    /* .fade-leave-active in <2.1.8 */

  {
    opacity: 0
  }
</style>



